'use client';

import React, { useEffect, useState } from "react";
import { useParams, useRouter } from 'next/navigation';
import '../../../public/styles/styles/game_start.css';
import { getAccessToken } from '../../../../token';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || "https://ec2-3-34-134-27.ap-northeast-2.compute.amazonaws.com/api/wargame-problems";
const FILE_BASE_URL = API_BASE_URL.replace('/api/wargame-problems', '') || "https://ec2-3-34-134-27.ap-northeast-2.compute.amazonaws.com";

interface Problem {
  title: string;
  creator: string;
  level: number;
  problemFile?: string;
  detail: string;
  dockerfileLink?: string;
}

interface Ranking {
  nickname: string;
  firstBlood: string;
}

interface Comment {
  id: number;
  creator: {
    nickname: string;
  };
  createdAt: string;
  content: string;
}

interface ApiResponse<T> {
  result: T;
}

const CTFProblemPage = () => {
  const params = useParams();
  const router = useRouter();
  const problemId = Array.isArray(params?.id) ? params.id[0] : params?.id || "6";

  const [problem, setProblem] = useState<Problem | null>(null);
  const [flag, setFlag] = useState<string>("");
  const [message, setMessage] = useState<string>("");
  const [comments, setComments] = useState<Comment[]>([]);
  const [newComment, setNewComment] = useState<string>("");
  const [ranking, setRanking] = useState<Ranking[]>([]);
  const [vmAddress, setVmAddress] = useState<string>("");
  const [token, setToken] = useState<string | null>(null);
  const [isTokenLoaded, setIsTokenLoaded] = useState(false);

  useEffect(() => {
    const storedToken = getAccessToken();
    setToken(storedToken);
    setIsTokenLoaded(true); // ÌÜ†ÌÅ∞ Î°úÎî© ÏôÑÎ£å
  }, []);

  useEffect(() => {
    if (!isTokenLoaded) return; // ÌÜ†ÌÅ∞ Î°úÎî© ÏôÑÎ£å Ï†ÑÏóêÎäî Ïã§ÌñâÌïòÏßÄ ÏïäÏùå

    if (!token) {
      alert("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú ÏÑúÎπÑÏä§ÏûÖÎãàÎã§.");
      router.push("/login"); // Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏
      return;
    }

    const fetchProblem = async () => {
      try {
        const res = await fetch(`https://ec2-3-34-134-27.ap-northeast-2.compute.amazonaws.com/api/problems/${problemId}`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Accept': '*/*',
          },
        });

        if (!res.ok) {
          throw new Error(`Î¨∏Ï†ú API Ìò∏Ï∂ú Ïã§Ìå®: ${res.status} ${res.statusText}`);
        }

        const data: ApiResponse<Problem> = await res.json();
        setProblem(data.result);
      } catch (error) {
        console.error("Î¨∏Ï†ú Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:", error);
      }
    };

    const fetchComments = async () => {
      try {
        const res = await fetch(`${FILE_BASE_URL}/api/problems/${problemId}/comments`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Accept': '*/*',
          },
        });

        if (!res.ok) {
          throw new Error(`ÎåìÍ∏Ä API Ìò∏Ï∂ú Ïã§Ìå®: ${res.status} ${res.statusText}`);
        }

        const data: ApiResponse<Comment[]> = await res.json();
        setComments(data.result);
      } catch (error) {
        console.error("ÎåìÍ∏Ä Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:", error);
      }
    };

    const fetchRanking = async () => {
      try {
        const res = await fetch(`${FILE_BASE_URL}/api/problems/${problemId}/firstblood?size=5`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Accept': '*/*',
          },
        });

        if (!res.ok) {
          throw new Error(`Îû≠ÌÇπ API Ìò∏Ï∂ú Ïã§Ìå®: ${res.status} ${res.statusText}`);
        }

        const data: ApiResponse<Ranking[]> = await res.json();

        // resultÍ∞Ä Î∞∞Ïó¥Ïù¥ ÏïÑÎãàÎ©¥ Í∞ùÏ≤¥Î°ú Î∞òÌôòÎê®
        if (Array.isArray(data.result)) {
          setRanking(data.result);
        } else {
          setRanking([data.result]); // Í∞ùÏ≤¥Î•º Î∞∞Ïó¥Î°ú Í∞êÏã∏ÏÑú ÏÉÅÌÉúÏóê Ï†ÄÏû•
        }
      } catch (error) {
        console.error("Îû≠ÌÇπ Ï°∞Ìöå Ïã§Ìå®:", error);
      }
    };

    fetchProblem();
    fetchComments();
    fetchRanking();
  }, [problemId, token, isTokenLoaded]);

  const handleSubmit = async () => {
    if (!token) return;

    try {
      const url = `${FILE_BASE_URL}/api/problems/${problemId}/solve?flag=${encodeURIComponent(flag)}`;

      const res = await fetch(url, {
        method: "GET",
        headers: {
          "Authorization": `Bearer ${token}`,
          "Accept": '*/*',
        },
      });

      if (!res.ok) {
        setMessage("Ï†ïÎãµ ÌôïÏù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
        return;
      }

      const data = await res.json();
      setMessage(data.result ? "Ï†ïÎãµÏûÖÎãàÎã§! Ï∂ïÌïòÌï©ÎãàÎã§." : "Ïò§ÎãµÏûÖÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌïòÏÑ∏Ïöî.");
    } catch (error) {
      console.error("Ï†ïÎãµ ÌôïÏù∏ Ïã§Ìå®:", error);
      setMessage("Ï†ïÎãµ ÌôïÏù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    }
  };

  const handleAddComment = async () => {
    if (newComment.trim() === "") return;

    try {
      const res = await fetch(`${API_BASE_URL}/api/comments`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          type: "PROBLEM", // typeÏùÑ "PROBLEM"ÏúºÎ°ú ÏÑ§Ï†ï
          parentId: 0, // Î∂ÄÎ™® ÎåìÍ∏Ä IDÎäî 0ÏúºÎ°ú ÏÑ§Ï†ï
          contents: newComment, // Í∏∞Ï°¥ contentÎäî contentsÎ°ú Î≥ÄÍ≤Ω
        }),
      });

      if (!res.ok) {
        throw new Error(`ÎåìÍ∏Ä ÏûëÏÑ± Ïã§Ìå®: ${res.status} ${res.statusText}`);
      }

      const data = await res.json();
      setComments((prevComments) => [...prevComments, data.result]);
      setNewComment(""); // ÎåìÍ∏Ä ÏûëÏÑ± ÌõÑ ÏûÖÎ†•Í∞í Ï¥àÍ∏∞Ìôî
    } catch (error) {
      console.error("ÎåìÍ∏Ä ÏûëÏÑ± Ïã§Ìå®:", error);
    }
  };

  const handleShowVmAddress = () => {
    setVmAddress(problem?.dockerfileLink || "VM Ï£ºÏÜåÍ∞Ä Ï†úÍ≥µÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.");
  };

  if (!problem) return <div>Î°úÎî© Ï§ë...</div>;

  const formatTime = (timeString: string) => {
    const date = new Date(timeString);
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const hours = date.getHours();
    const minutes = date.getMinutes();
    const seconds = date.getSeconds();

    return `${year}-${month < 10 ? '0' : ''}${month}-${day < 10 ? '0' : ''}${day} ${hours}:${minutes < 10 ? '0' : ''}${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
  };

  return (
    <div>
      <div className="window">
        <div className="title-bar">
          <span className="title">{problem.title}</span>
          <span className="level" style={{ color: "gold" }}>
            {"‚≠ê".repeat(problem.level)}
          </span>
          <button className="close-button">X</button>
        </div>

        {problem.problemFile ? (
          <p>
            Î¨∏Ï†ú ÌååÏùº:{" "}
            <a
              href={`${FILE_BASE_URL}/${problem.id}/download`}
              download
              target="_blank"
              rel="noopener noreferrer"
              className="download-link"
            >
              Îã§Ïö¥Î°úÎìú
            </a>
          </p>
        ) : (
          <p>Î¨∏Ï†ú ÌååÏùº ÏóÜÏùå</p>
        )}
        <p>{problem.detail}</p>

        {/* ÌîåÎûòÍ∑∏ ÏûÖÎ†• Î∞ïÏä§ÏôÄ Ï†úÏ∂ú Î≤ÑÌäº */}
        <div>
          <input
            type="text"
            placeholder="ÌîåÎûòÍ∑∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            value={flag}
            onChange={(e) => setFlag(e.target.value)}
            className="flag-input"
          />
          <button onClick={handleSubmit} className="flag-submit-button">
            Ï†úÏ∂ú
          </button>
        </div>

        {message && <div className="message">{message}</div>}

        <div className="ranking-box">
          <h3>Îû≠ÌÇπ</h3>
          {ranking.length > 0 ? (
            ranking.map((user, index) => (
              <div key={index} className="ranking-item">
                <span className="rank">{index === 0 ? "üëë" : index + 1}</span>
                <span className="username">{user.nickname}</span>
                <span className="solved-time">{formatTime(user.firstBlood)}</span>
              </div>
            ))
          ) : (
            <div>ÏïÑÏßÅ ÌíÄÏù¥Ìïú ÏÇ¨Ïö©ÏûêÍ∞Ä ÏóÜÏäµÎãàÎã§.</div>
          )}
        </div>
      </div>

      <div className="card">
        <h2>VM Ï£ºÏÜå</h2>
        <button onClick={handleShowVmAddress} className="button vm-button">
          VM Ï£ºÏÜå Î≥¥Í∏∞
        </button>
        {vmAddress && <div className="vm-address">{vmAddress}</div>}
      </div>

      <div className="comment-box">
        <h2>ÎåìÍ∏Ä</h2>
        <textarea
          placeholder="ÎåìÍ∏ÄÏùÑ ÏûëÏÑ±ÌïòÏÑ∏Ïöî"
          value={newComment}
          onChange={(e) => setNewComment(e.target.value)}
          className="textarea"
        />
        <button onClick={handleAddComment} className="button comment-button">
          ÎåìÍ∏Ä ÏûëÏÑ±
        </button>
        <div className="comment-list">
          {comments.map((comment) => (
            <div key={comment.id} className="comment-item">
              <p>
                <strong>{comment.creator?.nickname || "ÏùµÎ™Ö"}</strong> <span>{formatTime(comment.createdAt)}</span>
              </p>
              <p>{comment.content}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default CTFProblemPage;
